<template>
    <div class="recommend">
      <div v-if="$route.name === '返回小说推荐'">
        <Button type="success" size="large" @click="modal = true" class="create">新增返回小说列表</Button>
        <Table class="recommend-table" :columns="recommendHeader" :data="recommendData"></Table>
        <!-- <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage" /> -->
        <div class="modal" v-if="modal">
          <h3>新建小说推荐列表</h3><br>
          <Form :model="form" :label-width="80">
            <FormItem label="类型">
              <RadioGroup v-model="form.type">
                <Radio label="0">微信内</Radio>
                <Radio label="1" disabled>微信外</Radio>
              </RadioGroup>
            </FormItem>
            <FormItem label="统计代码">
              <Input v-model="form.listCode" placeholder="---选填---"></Input>
            </FormItem>
          </Form>
          <Button class="btn" type="warning" @click="modalClose">取消</Button>
          <Button class="btn" type="success" @click="modalSubmit">确定</Button>
        </div>
      </div>
      <router-view></router-view>
    </div>
</template>

<script>
import Clipboard from "clipboard";
new Clipboard(".copyBtn");
export default {
  data() {
    return {
      form: {
        type: '0'
      },
      domain_names: [],
      modal: false,
      recommendData: [],
      recommendHeader: [
        {
          key: 'id',
          title: 'ID',
          align: 'center'
        }, {
          title: '类型',
          align: 'center',
          render: (h, params) => {
            let type = params.row.type == 0 ? '微信内' : '微信外'
            return h('span', {
              props: {}
            }, type)
          }
        }, {
          title: '复制链接',
          align: 'center',
          render: (h, params) => {
            let that = this;
            return h('Button', {
              class: "copyBtn",
              attrs: {
                "data-clipboard-text":
                  that.domain_names[0].domain_name +
                  "/recommend/list/" + params.row.id
              },
              props: {
                size: 'small',
                type: 'success'
              },
              on: {
                click() {
                  that.$Message.info("复制成功");
                }
              }
            }, '复制')
          }
        }, {
          title: 'Action',
          align: 'center',
          render: (h, params) => {
            let that = this;
            return h('div', [
              h('Button', {
                style: {
                  marginRight: '10px'
                },
                props: {
                  size: 'small',
                  type: 'primary'
                },
                on: {
                  click() {
                    that.$router.push({
                      name: '修改小说返回列表',
                      query: {id: params.row.id, type: params.row.type, _id: params.row._id}
                    })
                  }
                }
              }, '修改'),
              h('Button', {
                props: {
                  size: 'small',
                  type: 'error'
                },
                on: {
                  click() {
                    that.$Modal.confirm({
                      title: '确认删除吗？',
                      onOk: () => {
                        that.$axios.get('/recommend/list/del', {params: {id: params.row._id}})
                        .then(res => {
                          that.$Message.info('删除成功')
                          that.novelData.splice(params.index, 1)
                        })
                      }
                    })
                  }
                }
              }, '删除'),
            ])
          }
        }
      ]
    };
  },
  mounted() {
    this.getDomain()
  },
  methods: {
    showList() {
      this.$axios.get('/recommend/list/show')
      .then(res => {
        this.recommendData = res.data.data
      })
    },
    modalClose() {
      this.form = {
        type: '0'
      }
      this.modal = false
    },
    modalSubmit() {
      this.$axios.post('/recommend/list/create', this.form)
      .then(res => {
        if(res.data.success) {
          this.$Message.info(res.data.success)
          this.modalClose()
          this.$router.push({
            name: '创建小说返回列表',
            query: res.data.data
          })
        }
      })
    },
    getDomain() {
      this.$axios.get("/tuiguang/novel/show").then(res => {
        this.domain_names = res.data.domain_names;
        this.showList();
      });
    }
  },
  watch: {
    // $route(to, from) {
    //   if(this.$route.query.state != 'cancel') {
    //     if(from.name == '创建推广链接') {
    //       this.spreadData.push(this.$route.query)
    //     } else if (from.name == '修改推广链接') {
    //       this.spreadData.splice(this.$route.query.index, 1, this.$route.query.row)
    //     }
    //   }
    // }
  }
};
</script>


<style scoped>
.recommend-table {
  margin: 30px 0;
}
.modal {
  width: 300px;
  /* height: 180px; */
  padding: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -165px;
  margin-top: -105px;
  background: #eee;
  z-index: 100000;
  color: #999;
}
.modal .btn {
  margin-left: 20px;
}
</style>
